.title div {
  display: inline-block;
}

.titleText {
  font-family: inherit;
  font-size: 0.875rem;
  margin: 1rem;
}

.titleText span {
  margin-top: 3px;
  margin-right: 10px;
  display: inline-block;
  background: #f48fb1;
  width: 1rem;
  height: 1rem;
  border-radius: 5px;
  box-shadow: 2px 2px 5px #facece;
}
