/**/

.row::after {
  content: "";
  clear: both;
  /* display: table; */
}

.col1 {width: 8.33%; float: left; padding: 15px;}
/* .col2 {display: none;} */
.col3 {width: 50%; float: left; margin: 1rem;}
.col4 {width: 33.33%; float: left; padding: 15px;}
.col5 {width: 41.66%; float: left; padding: 15px;}
.col6 {width: 50%; float: left; padding: 15px;}
.col7 {width: 58.33%; float: left; padding: 15px;}
.col8 {width: 66.66%; float: left; padding: 15px;}
.col9 {width: 75%; float: left; padding: 15px;}
.col10 {width: 83.33%; float: left; padding: 15px;}
.col11 {width: 91.66%; float: left; padding: 15px;}
.col12 {width: 100%; float: left; padding: 15px;}

.col2 {
  position: fixed;
  top: 0;
  left: -100%;
  width: 15rem;
  height: 100%;
  z-index: 10;
  background: #f5f2f2;
  border-right: 2px solid #dddddd;
  box-shadow: 2px 2px 5px #dddddd;
  transition: left 300ms cubic-bezier(1, 0, 0, 1);
}

.col2Open {
  left: 0;
}

@media (min-width: 32rem) {
  .col2 {
    position: fixed;
    display: block;
    width: 19.66%;
    float: left;
    left: 0;
    height: 100vh;
  }
}

@media (min-width: 32rem) {
  .row {
    margin-left: 18%;
  }
  .col3 {
    width: 22rem;
    height: 22.75rem;
    float: left;
  }
}

.inputArea input,
.inputArea span   {
  display: block;
  width: 50rem;
}

.sideBarMenu {
  width: 20%;
  float: left;
}

.mainContent {
  width: 75%;
  float: left;
}

.charts {
  width: 80%;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}
